
import { Box, Col, Content, Row } from "adminlte-2-react";
import { useEffect, useState } from "react";
import { SETTINGS_CONFIG } from "src/constants/global";
import SettingService from "src/services/SettingService";

const READ_SETTINGS = [
    SETTINGS_CONFIG.EMAIL_SALES,
    SETTINGS_CONFIG.LANDLINE_SALES,
    SETTINGS_CONFIG.LANDLINE_SUPPORT,
    SETTINGS_CONFIG.EMAIL_SUPPORT
]
const ContactUsView = ({
    title = 'Contact Us',
    description = 'contact information',
    ...rest
}) => {
    const [settings, setSettings] = useState([])
    useEffect(() => {
        (async () => {
            const data = await SettingService.getWebsiteSettings()
            if (data?.success && data?.data?.results) {
                const _settings = (data.data.results || []).filter(setting => READ_SETTINGS.includes(setting.name))
                setSettings(_settings)
            }
        })()
    }, [])

    return (
        <Content title={title} subTitle={description} browserTitle={"Contact us"}>
            <Box type="success" title="Displaying contact information" icon="fa-phone-alt">
                <Row>
                    <Col md={12}>
                        <table className="table table-bordred table-striped">
                            <tbody>
                            {settings.map((setting) => {
                                return (
                                    <tr>
                                        <td>{setting.label}</td>
                                        <td>{setting.value || setting.default}</td>
                                    </tr>
                                );
                            })}
                            </tbody>
                        </table>
                    </Col>
                </Row>
            </Box>
        </Content>
    );
};

export default ContactUsView;
