import { createSlice } from "@reduxjs/toolkit";

export const { actions: GlobalActions, reducer: GlobalReducer } = createSlice({
  name: "globalSlice",
  initialState: {
    errors: null,
    globalMessage: {
      title: '',
      type: '',
      message: ''
    },
    settings: [],
    snmpDeviceStatus: {}
  },
  reducers: {
    setGlobalMessage: (state, { payload }) => {
      const { type, title, message } = payload
      state.globalMessage = { ...state.globalError, type, title, message }
    },
    setWebsiteSettings: (state, { payload }) => {
      const { results } = payload
      state.settings = results
    },
    setSnmpDeviceStatus: (state, { payload }) => {
      state.snmpDeviceStatus = payload
    }
  },
});
