
import { Box, Col, Content, Row } from "adminlte-2-react";
import DataGridComponent from "src/components/DataGridComponent";
import { useMemo, useState } from "react";
import SettingService from "src/services/SettingService";
import PhotoThumb from "src/components/PhotoThumb";
import { connect } from "react-redux";
import { GlobalActions } from "src/slices/globalSlice";

const SettingsListView = ({
  title = 'Configurations',
  description = 'here you can manage configurations',
  triggerGetSettings,
  ...rest
}) => {

  const [settings, setSettings] = useState([])
  const [record, setCurrentRecord] = useState(null)

  const fields = useMemo(() => {
    if (record) {
      return settings.filter(setting => setting.id === record.id)
    }
    return []
  }, [record, settings])

  return (
    <Content title={title} subTitle={description} browserTitle={"Manage Configuration"}>
      <Box type="primary" title="Displaying configurations" icon="fa-cog">
        <Row>
          <Col md={12}>
            <DataGridComponent
              {...rest}
              hideIdOnEdit={true}
              entityPlural="Configurations"
              entitySingle="Configuration"
              canAdd={false}
              canEdit={true}
              canDelete={false}
              resource={`/api/v1/admin/settings`}
              onFormShow={(row) => {
                setCurrentRecord(row)
              }}
              onDataUpdated={(data) => {
                triggerGetSettings()
                setSettings(data.results)
              }}
              columns={[
                {
                  dataField: 'label',
                  text: 'Setting Name',
                  sort: true,
                },
                {
                  dataField: 'value',
                  text: 'Setting Value',
                  sort: true,
                  formatter: (c, row) => {
                    if (row.type === 'upload') {
                      return <PhotoThumb border={false} width={50} height={50} url={row.value} />
                    }
                  }
                }
              ]}
              formOptions={
                {
                  fields
                }
              }
            />
          </Col>
        </Row>
      </Box>
    </Content>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    triggerGetSettings: async () => {
      const data = await SettingService.getWebsiteSettings()
      data?.success && dispatch(GlobalActions.setWebsiteSettings(data.data))
    }
  };
};

export default connect(null, mapDispatchToProps)(SettingsListView);
