import httpClient from "src/utils/httpClient";

const getWebsiteSettings = async () => await httpClient.get("/api/v1/admin/settings");
const saveWebsiteSetting = async (settingData) => await httpClient.put("/api/v1/admin/settings", settingData);

const SettingService = {
  getWebsiteSettings,
  saveWebsiteSetting
};

export default SettingService;
