import React from "react";
import { useEffect, useState } from "react";
import CommonServerice from "src/services/CommonService";
import { pushQueryParams } from "src/utils/routerHelper";

const { default: EntityForm } = require("../EntityForm")

const LocationFilter = ({
    ...rest
}) => {
    console.log(rest)
    const [locations, setLocations] = useState([])
    useEffect(() => {
        (async () => {
            setLocations(await CommonServerice.getLocationList())
        })();
    }, [])

    return (
        <EntityForm
        hideSubmit={true}
        fields={[
            {
                id: 'location_filter',
                name: 'location_filter',
                label: 'Filter by Location',
                type: 'select',
                cols: 4,
                hideLabel: true,
                onChange: (e) => {
                    const value = e.nativeEvent.target.value
                    pushQueryParams(rest.history, {
                        locationId: value || null
                    }, true)
                },
                options: locations.map(l => {
                    return {
                        key: l.id,
                        value: l.title
                    }
                })
            }
        ]}
        />
    )
}

export default LocationFilter